.chatContainer {
    display: flex;
    flex-direction: column;
    flex: 1;
    width: 100%;
    height: 100%;
    max-width: 728px;
    margin: auto;
    /* background-color: black; */
    a {
      color: var(--colorNeutralForeground4);
    }
  }
  
  .chatField {
    display: flex;
    flex: 1;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 24px;
  }
  
  .promptButtonContainer {
    display: flex;
    gap: 12px;
  }
  
  .promptButton {
    display: flex;
    flex-direction: column;
    width: 100%;
    border: 1px solid var(--colorNeutralStroke2);
    padding: 12px;
    gap: 8px;
    border-radius: 8px;
    cursor: pointer;
    color: var(colorNeutralForeground1);
    background-color: var(--colorNeutralBackground1);
  }
  
  .promptButton:hover {
    border: 1px solid var(--colorNeutralStroke1Hover);
    background-color: var(--colorNeutralBackground1Hover);
  }
  
  .promptButton:active {
    color: var(colorNeutralForeground1Hover);
    border: 1px solid var(--colorNeutralStroke1Pressed);
    background-color: var(--colorNeutralBackground1Pressed);
  }
  
  .chatInputContainer {
    margin: 16px 0;
    display: flex;
    background-color: var(--colorNeutralBackground1);
    padding: 8px;
    border-radius: 12px;
    flex-direction: column;
    gap: 8px;
    border: 1px solid transparent;
  }
  
  .chatInput {
    display: flex;
    width: 100%;
    background-color: transparent;
    font-size: 1rem;
    box-shadow: none;
    border: none;
    color: var(--colorNeutralForeground1);
    padding: 6px;
    align-items: center;
  }
  
  .chatInput:focus {
    outline: none;
  }
  
  .chatInputContainer:focus-within {
    border: 1px solid var(--colorNeutralStroke1Pressed);
  }
  
  .chatInput::placeholder {
    color: var(--colorNeutralForeground4);
  }
  
  .chatTools {
    display: flex;
    justify-content: space-between;
  }
  
  .chatHistoryContainer {
    padding: 8px;
    overflow: scroll;
    overflow-x: hidden;
  }
  
  .chatHistoryItem {
    display: flex;
    flex: 1;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    font-size: 0.875rem;
    padding: 8px;
    border-radius: 4px;
    border: none;
    background-color: transparent;
    color: var(--colorNeutralForeground2);
    cursor: pointer;
    height: 36px;
    text-transform: capitalize;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    p {
      width: 10%;
    }
    div {
      flex: 1;
    }
  }
  
  .chatHistoryItemTitle {
    display: flex;
    flex: 1;
    width: 100%;
    overflow: hidden;
  }
  
  .chatHistoryItemDismiss {
    color: var(--colorNeutralForeground4);
    display: none;
    height: 20px;
    width: 20px;
  }
  
  .chatHistoryItemDismiss:hover {
    color: var(--colorNeutralForeground1Hover);
  }
  
  .chatHistoryItem:hover {
    background-color: var(--colorNeutralBackground4Hover);
    span.chatHistoryItemDismiss {
      display: initial;
    }
  }
  

  .LeftNavItem {
    display: flex;
    align-content: center;
    align-items: center;
    padding: 6px 8px;
    border-radius: 4px;
    gap: 6px;
    color: var(--colorNeutralForeground4);
    cursor: pointer;
    font-size: .9rem;
  }

  .LeftNavItem:hover {
    background-color: var(--colorNeutralBackground4Hover);
    color: var(--colorNeutralForeground2hover);
  }

  .LeftNavItem:active {
    background-color: var(--colorNeutralBackground4Pressed);
    color: var(--colorNeutralForeground2Hover);
  }

  .LeftNavItem .icon {
 height: 20px;
  }
  
